<template>
    <a-modal
        v-model:open="modalVisible"
        :title="$t('battleLog')"
        width="1024px"
    >
        <p>结果：{{ fightLog[fightLog.length - 1].sub }} 胜</p>
        <span>战利品：</span>
        <span v-if="spoils.length === 0">无</span>
        <template v-else>
            <span v-for="item in spoils" style="margin-right: 5px">
                {{ item.name }}
            </span>
        </template>
        <template #footer>
            <a-button type="primary" @click="submit" @cancel="submit">
                ok
            </a-button>
        </template>
        <div class="LogContent">
            <div class="logs" v-for="(item, index) in fightLogRender">
                <p>{{ $t("round") }} {{ index + 1 }}</p>
                <div class="logsItem">
                    <div class="sub">
                        <span>{{ item.sub }}</span>
                        <span class="oriHp">
                            <span
                                class="hp"
                                :style="`width: ${item.aggressor.hpPercent}px`"
                            >
                            </span>
                        </span>
                        <span>{{ item.aggressor.hpPercent }}%</span>
                        <span>
                            hp: {{ item.aggressor.hp }}/{{
                                item.aggressor.oriHp
                            }}
                        </span>
                        <span>waigong: {{ item.aggressor.waigong }}</span>
                        <span>neigong: {{ item.aggressor.neigong }}</span>
                        <span>waifang: {{ item.aggressor.waifang }}</span>
                        <span>neifang: {{ item.aggressor.neifang }}</span>
                        <span>huixin: {{ item.aggressor.huixin }}</span>
                        <span>huishang: {{ item.aggressor.huishang }}</span>
                        <span>renxing: {{ item.aggressor.renxing }}</span>
                    </div>
                    <div class="detail">
                        {{ item.aggressor.nickname }} 对
                        {{ item.victim.nickname }} 造成了 {{ item.damage }}点
                        {{ item.crit ? "暴击伤害" : "伤害" }}
                    </div>
                    <div class="obj">
                        <span>{{ item.obj }}</span>
                        <span class="oriHp">
                            <span
                                class="hp"
                                :style="`width: ${item.victim.hpPercent}px`"
                            >
                            </span>
                        </span>
                        <span>{{ item.victim.hpPercent }}</span>
                        <span>
                            hp: {{ item.victim.hp }}/{{ item.victim.oriHp }}
                        </span>
                        <span>waigong: {{ item.victim.waigong }}</span>
                        <span>neigong: {{ item.victim.neigong }}</span>
                        <span>waifang: {{ item.victim.waifang }}</span>
                        <span>neifang: {{ item.victim.neifang }}</span>
                        <span>huixin: {{ item.victim.huixin }}</span>
                        <span>huishang: {{ item.victim.huishang }}</span>
                        <span>renxing: {{ item.victim.renxing }}</span>
                    </div>
                </div>
            </div>
        </div>
    </a-modal>
</template>

<script>
import { computed, onMounted, reactive, toRefs, watch } from "vue";

export default {
    name: "FightModal",
    props: {
        modalVisible: {
            default: false,
            type: Boolean,
        },
        fightLog: {
            default: [],
            type: Array,
        },
        spoils: {
            default: [],
            type: Array,
        },
    },
    emits: ["submit"],
    setup(props, ctx) {
        const state = reactive({});
        const submit = () => {
            ctx.emit("submit");
        };

        // 计算 添加 血量百分比
        const fightLogRender = computed(() => {
            // 血量百分比
            let aggressorHpPercent = 0,
                victimHpPercent = 0;

            return props.fightLog.map((i, index) => {
                // 攻击者
                // 奇数 主角 偶数 敌方
                let aggressor = {}, // 攻击者
                    victim = {}; // 被攻击者
                if (index % 2 === 0) {
                    aggressor = JSON.parse(JSON.stringify(i.myAttr));
                    victim = JSON.parse(JSON.stringify(i.enemyAttr));
                } else {
                    aggressor = JSON.parse(JSON.stringify(i.enemyAttr));
                    victim = JSON.parse(JSON.stringify(i.myAttr));
                }

                // 血量百分比
                aggressorHpPercent = (aggressor.hp / aggressor.oriHp) * 100;
                victimHpPercent = (victim.hp / victim.oriHp) * 100;

                let res = {
                    ...i,
                    aggressor: {
                        ...aggressor,
                        hpPercent:
                            aggressorHpPercent > 0
                                ? aggressorHpPercent.toFixed(2)
                                : "0",
                    },
                    victim: {
                        ...victim,
                        hpPercent:
                            victimHpPercent > 0
                                ? victimHpPercent.toFixed(2)
                                : "0",
                    },
                };
                return res;
            });
        });

        return {
            ...toRefs(props),
            ...toRefs(state),
            submit,
            fightLogRender,
        };
    },
};
</script>

