    height: 400px;
    overflow-y: auto;
    margin: 20px auto;

    .logs {
        border-radius: 8px;
        padding: 15px;
        border: 1px solid #d2d2d2;
        margin-bottom: 10px;

        .logsItem {
            display: flex;
            align-items: center;
            justify-content: space-between;

            > div {
                width: 33.33%;
            }

            .oriHp {
                width: 100px;
                height: 10px;
                border: 1px solid #d2d2d2;
                position: relative;

                .hp {
                    display: inline-block;
                    width: 50%;
                    height: 100%;
                    background-color: #ff5f37;
                    position: absolute;
                    left: 0;
                }
            }

            .sub {
                display: flex;
                flex-direction: column;
            }

            .detail {
                text-align: center;
                //border-left: 1px solid #d2d2d2;
                //border-right: 1px solid #d2d2d2;
            }

